@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-shadow: border-box;
  color: #5d5d5d;
}
.container{
  margin: 0 auto;
  width:780px;
  
}

.cityList-container{
    width: 60%;
    padding: 60px;
}
.heading{
    text-align: center;
    margin-bottom: 20px;
    font-size: 22px;
    font-weight: 600;
}
.cityList{
    list-style: none;
    margin: 0px;
    padding: 0px;
}
.cityList li{
    text-align: center;
    padding: 10px;
    margin-bottom: 10px;
}
.cityList li .btn{
    padding: 13px;
    border-radius: 44px;
    border: none;
    background: #0abe51;
    width: 195px;
    color: #fff;
    cursor: pointer;
    outline:none;
}
/* Image css */
.resto-details-ctn {
    padding-top: 20px;
}

.details-liked {
    color: #F44336;
    font-size: 25px;
    margin-left: 10px;
    vertical-align: text-top;
}

.likedText {
    font-size: 12px;
    font-weight: 700;
    margin-left: 9px;
}

.details-img {
    width: 100%;
    height: 320px;
    object-fit: cover;
    object-position: center;
}
/* basic info css */
.resto-info{
    padding: 20px 0px;
    position: relative;
}
.resto-info .rating-info{
    padding: 10px 0px;
   
}
.resto-name{
    font-size: 22px;
    margin: 0px;
    font-weight: 500;
    color: #5d5d5d;
}

.resto-rating{
    font-size: 14px;
    font-weight: 500;
    margin-right: 5px;
}

.resto-vote{
    font-size: 12px;
    font-weight: 400;
    padding-right: 10px;
    border-right: 1px solid #e2cfcf;
    color: #9E9E9E;
}
.review-count{
    font-size: 12px;
    font-weight: 600;
    margin-left: 10px;
}
.share-btn{
    position: absolute;
    top: 22px;
    right: 20px;
    border-radius: 13px;
    border: none;
    font-size: 13px;
    padding: 5px 15px;
    background: #4CAF50;
    color: #fff;
    cursor: pointer;
    outline: none;
}
/* Cusinine info css */
.cuisine-info{
    padding: 8px 0 25px 0px;
    position: relative;
    border-bottom: 1px solid #e2cfcf;
}
.cuisine-info .cuisine-items{
    font-weight: 400;
    color: #E91E63;
    font-size: 15px;
}
.cost{
    position: absolute;
    right: 0px;
    font-weight: 700;
    color: #5d5d5d;
    font-size: 15px;
    border: 1px solid;
    padding: 4px 11px;
    border-radius: 16px;
    top: 2px;
}
/* Address css */
.resto-address .info{
    padding: 20px 0 10px 0px;
    font-size: 13px;
    font-weight: 600;
}
.resto-address .a-info{
    padding-bottom: 20px;
    font-size: 13px;
    font-weight: 600;
}

/* photo and menu css */
.btn-section{
    padding: 10px 0px 24px 0px;
    border-bottom: 1px solid #e2cfcf;
}
.btn-section .btn{
    padding: 10px 15px;
    width: 20%;
    border: none;
    border-radius: 5px;
    background: #0abe51;
    color: #fff;
    margin-right: 24px;
    cursor: pointer;
    outline: none;
}
.btn-section .btn .btn-icon{
    margin-right: 10px;
    color: #fff;
}

/* Reviews css */
.reviews .head{
    margin-top: 25px;
    margin-bottom: 34px;
    font-weight: 600;
    color: #5d5d5d;
}
.reviews ul{
    margin-top: 20px;
    padding: 0px;
    list-style: none;
}
.reviews li{
    clear: both;
    margin-bottom: 35px;
}
.reviews .user-image{
    border-radius: 50%;
    height: 50px;
    width: 50px;
    float: left;
    margin-right: 20px;
}
.reviews .user-name{
   font-size: 14px;
   font-weight: 700;
   margin-bottom: 5px;
}
.reviews .reviews-info{
    margin-left: 70px;
}
.reviews .review-text{
    font-size: 14px;
    color: gray;
    margin-top: 10px;
}
.stars{
    color:#FF9800;
    font-size: 12px;
    letter-spacing: 3px;
}
.more-btn{
    font-size: 13px;
    padding: 14px 19px;
    font-weight: 600;
    border: none;
    border-radius: 5px;
    background: #0abe51;
    color: #fff;
    margin-right: 24px;
    cursor: pointer;
    margin-top: 15px;
    outline: none;
}


.spinner {
    height: 24px;
    width: 24px;
    -webkit-animation: rotate 0.8s infinite linear;
            animation: rotate 0.8s infinite linear;
    border: 1px solid #0abe51;
    border-right-color: transparent;
    border-radius: 20px;
    margin: 30px auto;
  }
  
  @-webkit-keyframes rotate {
    0%    { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100%  { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes rotate {
    0%    { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100%  { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
.heading-title{
    margin: 40px 20px 10px;
    font-size: 18px;
    font-weight: 500;
}
a{
    text-decoration: none;
    color: #5d5d5d;
}
.resto-image{
    padding:20px;
    width:200px;
    float: left;
    margin-right: 20px;
    cursor: pointer;
}
.clear {
    clear: both;
}
.resto-list{
    list-style: none;
    padding: 0px;
    margin: 0px;
}
.resto-list li{
    padding: 20px;
    border-bottom: 1px solid #e2d6d6;
   margin-bottom: 20px;
}
.resto-details{
    margin-left: 264px;
}
.resto-name{
    margin: 0px 0px 8px 0px;
    font-size: 16px;
    font-weight: 700;
    color: #5d5d5d;
    cursor: pointer;
}
.resto-rating{
    font-size: 14px;
    font-weight: 600;
    margin-right: 5px;
}
.resto-rating .star{
    margin-right: 4px;
    color:#FF9800;
}
.resto-votes{
    font-size: 12px;
    font-weight: 400;
    color: #9E9E9E;
}
.resto-cuisine{
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 16px;
    color: #E91E63;
}
.locality{
    margin: 0px;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 8px;
    cursor: pointer;
}
address{
    font-size: 13px;
    font-weight: 500;
    font-style: normal;
    margin-bottom: 10px;
}

.like-btn, .unlike-btn {
    border: none;
    outline: none;
    margin-left: 10px;
    background: #0abe51;
    color: #fff;
    padding: 4px 11px;
    border-radius: 4px;
    cursor: pointer;
}

.unlike-btn .filled-heart,  .like-btn .unfilled-heart{
    margin-right: 5px;
}

#listMoreLoader {
    text-align: center;
    font-size: 18px;
    padding: 30px;
}
header{
    background: #0abe51;
}

.header-title{
    font-size: 26px;
    padding: 10px;
    font-weight: 700;
    display: inline-block;
    color: #000000b5;
}
.header-title-2{
    font-size: 26px;
    margin-right: 13px;
    margin-left: 5px;
    font-weight: 700;
    display: inline-block;
    color: #fff;
}

.header-icon{
    font-size: 24px;
    color: #fff;
}
